<template>
	<div>
		<h3 class="page_title">轮播图图片管理</h3>
		<Tabs type="card">
	        <TabPane :label="banner.title" v-for="(banner,index) in banners" :key="index">
	        	<Table border :columns="banner.columns" :data="banner.list"></Table>
	        </TabPane>
	    </Tabs>
		
	</div>
</template>

<script>
	export default {
		data(){
			return {
				banners:[
					{
						title:'首页轮播图',
						columns: [
		                    {
		                        type: 'selection',
		                        width: 60,
		                        align: 'center'
		                    },
		                    {
		                        title: '轮播图ID',
		                        key: 'banner_id'
		                    },
		                    {
		                        title: '轮播图图片',
		                        key: 'banner_url'
		                    },
		                    {
		                    	title:'介绍',
		                    	key: 'banner_msg'
		                    },
		                    {
		                    	title:'标题',
		                    	key: 'banner_title'
		                    },
		                    {
		                    	title:'操作'	,
		                    	key:'operation',
		                    	render: (h, params) => {
		                            return h('div', [
		                                h('Button', {
		                                    props: {
		                                        type: 'primary',
		                                        size: 'small'
		                                    },
		                                    style: {
		                                        marginRight: '5px'
		                                    },
		                                    on: {
		                                        click: () => {
		                                            
		                                        }
		                                    }
		                              }, '编辑'),
		                                h('Button', {
		                                    props: {
		                                        type: 'error',
		                                        size: 'small'
		                                    },
		                                    on: {
		                                        click: () => {
		                                            
		                                        }
		                                    }
		                                }, '删除')
		                            ]);
		                        }
		                    }
		                ],
		                list: [
		                    {
		                    	banner_id: 100,
		                    	banner_url:'adminddd',
		                    	banner_msg: '这是一张轮播图',
		                    	banner_title:'轮播图1'
		                    }
		                ]
					}
				]
			}
		}
	}
</script>

<style>
	.page_title{
		font-size: 20px;
		font-weight: normal;
		text-align: left;
		line-height: 50px;
		border-bottom: 1px solid #cccccc;
		margin-bottom: 20px;
	}
	.ivu-table-wrapper{
		display: block !important;
	}
</style>


