<template>
	<div>
		<h3 class="page_title">用户列表</h3>
		<Table border ref="selection" :columns="columns" :data="userList"></Table>
		<Modal
	        title="Title"
	        v-model="modal"
	        :closable="false"
	         ok-text="确认修改">
	        <Form :model="modalData" :label-width="80">
	        	<FormItem label="ID：">
	        		<p v-html="modalData._id"></p>
		            <!--<Input v-model="modalData.id" placeholder="Enter something..."></Input>-->
		        </FormItem>
		        <FormItem label="用户名：">
		        	<p v-html="modalData.username"></p>
		            <!--<Input v-model="modalData.username" placeholder="Enter something..."></Input>-->
		        </FormItem>
		        <FormItem label="手机：">
		            <Input v-model="modalData.iphone" placeholder="Enter something..."></Input>
		        </FormItem>
		        <FormItem label="邮箱：">
		            <Input v-model="modalData.email" placeholder="Enter something..."></Input>
		        </FormItem>
		        <FormItem label="角色：">
		            <Input v-model="modalData.role" placeholder="Enter something..."></Input>
		        </FormItem>
		        <FormItem label="创建时间：">
		            <Input v-model="modalData.createDate" placeholder="Enter something..."></Input>
		        </FormItem>
	        </Form>
	    </Modal>
	    <div class="page">
	   		<Page :total="100" show-elevator></Page>
	    </div>
	</div>
</template>

<script>
	export default {
		data(){
			return {
				modal: false,
				modalData: {},
				columns: [
                    {
                        type: 'selection',
                        width: 60,
                        align: 'center'
                    },
                    {
                        title: 'ID',
                        key: '_id'
                    },
                    {
                        title: '用户名',
                        key: 'username'
                    },
                    {
                        title: '手机',
                        key: 'iphone'
                    },
                    {
                        title: '邮箱',
                        key: 'email'
                    },
                    {
                        title: '角色',
                        key: 'role'
                    },
                    {
                        title: '创建时间',
                        key: 'createDate'
                    },
                    {
                    	title:'操作'	,
                    	key:'operation',
                    	render: (h, params) => {
                            return h('div', [
                                h('Button', {
                                    props: {
                                        type: 'primary',
                                        size: 'small'
                                    },
                                    style: {
                                        marginRight: '5px'
                                    },
                                    on: {
                                        click: () => {
                                            this.modal = true;
                                           	this.modalData = params.row
                                        }
                                    }
                              }, '编辑'),
                                h('Button', {
                                    props: {
                                        type: 'error',
                                        size: 'small'
                                    },
                                    on: {
                                        click: () => {
                                            
                                        }
                                    }
                                }, '删除')
                            ]);
                        }
                    }
                ],
                userList: [
//                  {
//                  	id: 1,
//                  	username:'admin',
//                  	iphone:'13500000000',
//                  	email:'123@qq.com',
//                  	role: 10,
//                  	createDate: '2018/5/23',
//                  	
//                  }
                ]
			}
		},
		created(){
			this.$axios({
			    method:"POST",
			    url:'/user/getUsers',
			    data:{
			        firstName:"Fred",
			        lastName:"Flintstone"
			    }
			})
			.then(res=>{
				console.log(res)
				this.userList = res.data.users;
			});
		}
	}
</script>

<style>
	.page_title{
		font-size: 20px;
		font-weight: normal;
		text-align: left;
		line-height: 50px;
		border-bottom: 1px solid #cccccc;
		margin-bottom: 20px;
	}
	.page{
		margin-top: 30px;
	}
</style>


